.remove-copyrights-pane {
    [class*="copyrights-pane"] {
        display: none !important; // Hack
    }
}

.ymaps-2-1-79-map {


    .ymaps-2-1-79-map {
        border-radius: 12px;
    }

    .ymaps-2-1-79-inner-panes {
        border-radius: 12px;
    }

    .ymaps-2-1-79-zoom__scale {
        display: none;
    }

    .ymaps-2-1-79-controls__control {
        bottom: auto !important;
        left: auto !important;
        right: 16px !important;
        top: 195px !important;
    }

    .ymaps-2-1-79-control-popup-parent>ymaps {
        border-radius: 16px;
    }

    .ymaps-2-1-79-control-popup__content {
        border-radius: 16px;
    }

    .ymaps-2-1-79-zoom {
        height: auto !important;
        padding: 0;
        background: #FFFFFF;
        border: 1px solid rgba(93, 112, 134, 0.1);
        box-shadow: 0px 7px 10px rgba(0, 0, 0, 0.1);
        border-radius: 16px;
        width: 44px;

        .ymaps-2-1-79-float-button {
            padding-left: 0;
            width: 100%;
            position: static !important;
            display: flex;
            align-items: center;
            justify-content: center;
            height: 100%;
            box-shadow: none;
            border: none;
            border-radius: 16px;
        }

        .ymaps-2-1-79-zoom__minus {
            height: 44px;
            position: relative !important;

            &::before {
                content: '';
                width: 100%;
                height: 1px;
                background-color: rgba(0, 0, 0, 0.05);
                ;
                position: absolute;
                top: 0;
                left: 0;
            }

            .ymaps-2-1-79-zoom__icon {
                width: 18px;
                height: 18px;
                border: none;
                background-image: url('../img/minus.svg');
                background-size: cover;
                position: static;
            }
        }

        .ymaps-2-1-79-zoom__plus {
            height: 44px;

            .ymaps-2-1-79-zoom__icon {
                width: 18px;
                height: 18px;
                border: none;
                background-image: url('../img/plus.svg');
                background-size: cover;
                position: static;
            }
        }

        @media (max-width:$smallTablet-max) {
            display: none;
        }
    }

    .ymaps-2-1-79-route-panel-button {
        right: 40px !important;
        top: 67px !important;
    }

    .ymaps-2-1-79-controls__control_toolbar.ymaps-2-1-79-user-selection-none {
        position: absolute !important;
        top: 91px;
        right: 16px;
        margin-right: 0 !important;
        background: #FFFFFF;
        border: 1px solid rgba(93, 112, 134, 0.1);
        box-shadow: 0px 7px 10px rgba(0, 0, 0, 0.1);
        border-radius: 16px;

        .ymaps-2-1-79-float-button {
            max-width: none !important;
            width: 44px;
            height: 44px;
            border-radius: 16px;
            padding: 0;
            box-shadow: none;
            border: none;
            display: flex;
            align-items: center;
            justify-content: center;

            .ymaps-2-1-79-float-button-icon {
                position: static;
                width: 18px;
                height: 18px;
                border: none;
                background-size: cover;
                background-image: url('../img/fullscreen-map.svg');
            }
        }

        @media (max-width:$smallTablet-max) {
            top: 0px;
        }
    }
}

.ymaps-2-1-79-gotoymaps__container,
.ymaps-2-1-79-gototech {
    display: none !important;
}

.ymaps-2-1-79-control-popup.ymaps-2-1-79-control-popup_static {
    margin-top: -180px !important;
    margin-right: 61px !important;
    border-radius: 16px;

    .ymaps-2-1-79-control-popup__title {
        font-size: 22px;
        line-height: math.div(28, 20);
        color: var(--dark);
    }

    .ymaps-2-1-79-control-popup__header {
        border-bottom: none;
        text-align: center;
        padding: 16px 12px 0;
    }

    .ymaps-2-1-79-control-popup__layout {}

    .ymaps-2-1-79-control-popup__icon {
        display: none;
    }

    .ymaps-2-1-79-route-panel__inputs {
        padding: 0 16px 24px;
    }

    .ymaps-2-1-79-route-panel-types {
        display: flex;
        align-items: center;
    }

    .ymaps-2-1-79-route-panel-types__type {
        flex-grow: 1;
        border-radius: 4px;
        background-position: 50% 50%;
        transition: background-color $transitionSpeed $transitionFunction,
            opacity $transitionSpeed $transitionFunction;
    }

    .ymaps-2-1-79-route-panel-types__type_selected {
        background-color: var(--hover-primary);
    }

    .ymaps-2-1-79-route-panel-input {
        border-radius: 5px;

        &.ymaps-2-1-79-route-panel-input_focused {
            box-shadow: inset 0 0 0 1px var(--blue);
        }
    }

    .ymaps-2-1-79-route-panel-input__icon {
        top: 10px;
    }

    .ymaps-2-1-79-route-panel-input__location {
        background-position: 50% 50%;
    }

    input.ymaps-2-1-79-route-panel-input__input {
        height: 38px;
    }
}


.yandex-map-active-fullscreen {
    .ymaps-2-1-79-control-popup.ymaps-2-1-79-control-popup_static {
        margin-top: -121px !important;
        margin-right: 62px !important;
    }

    .ymaps-2-1-79-float-button.ymaps-2-1-79-_hidden-text.ymaps-2-1-79-_checked {
        position: relative;
        z-index: 99;
        background-color: #fff;
    }
}

.ymaps-2-1-79-controls-pane{
    >.ymaps-2-1-79-controls__control:last-child{
        z-index: -1;
    }
}

.map-primary{
    &._routes{
        .ymaps-2-1-79-map .ymaps-2-1-79-controls__control_toolbar.ymaps-2-1-79-user-selection-none{
            top: 4px;
        }
        .ymaps-2-1-79-map .ymaps-2-1-79-controls__control{
          
        }
    }
}